import React, { useEffect, useState } from "react";
// import axios from 'axios';
import { useTable, usePagination, useSortBy } from "react-table";
import { Card, CardBody, CardTitle } from "reactstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import DataTablePagination from "../../DatatablePagination";

function Table({ columns, data }) {
  const {
    getTableProps,
    getTableBodyProps,
    prepareRow,
    headerGroups,
    page,
    canPreviousPage,
    canNextPage,
    pageCount,
    gotoPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0, pageSize: 6 },
    },
    useSortBy,
    usePagination
  );

  return (
    <>
      <table {...getTableProps()} className="r-table table">
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column, columnIndex) => (
                <th
                  key={`th_${columnIndex}`}
                  {...column.getHeaderProps(column.getSortByToggleProps())}
                  className={
                    column.isSorted
                      ? column.isSortedDesc
                        ? "sorted-desc"
                        : "sorted-asc"
                      : ""
                  }
                >
                  {column.render("Header")}
                  <span />
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map((row) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell, cellIndex) => (
                  <td
                    key={`td_${cellIndex}`}
                    {...cell.getCellProps({
                      className: cell.column.cellClass,
                    })}
                  >
                    {cell.render("Cell")}
                  </td>
                ))}
              </tr>
            );
          })}
        </tbody>
      </table>

      <DataTablePagination
        page={pageIndex}
        pages={pageCount}
        canPrevious={canPreviousPage}
        canNext={canNextPage}
        pageSizeOptions={[4, 10, 20, 30, 40, 50]}
        showPageSizeOptions={false}
        showPageJump={false}
        defaultPageSize={pageSize}
        onPageChange={(p) => gotoPage(p)}
        onPageSizeChange={(s) => setPageSize(s)}
        paginationMaxSize={pageCount}
      />
    </>
  );
}

const ModelServers = ({ textHead }) => {
  const [serverList, setServerList] = useState([]);
  const [loading, setLoading] = useState(false);

  const getAllServers = () => {
    setLoading(true);
    fetch(`/platform/api/models/servers`)
      .then((res) => res.json())
      .then((res) => setServerList(res.modelData));
    setLoading(false);
  };

  useEffect(() => {
    getAllServers();
  }, []);

  
  const cols = React.useMemo(
    () => [
      {
        Header: "Status",
        accessor: (row) => {
          if (row.status === "Available"){
            return (
              <i className="simple-icon-check text-success font-weight-bold h5 status-icon availableIcon" />
            );
          }
          return (
            <i className="iconsminds-repeat-2 text-primary font-weight-bold h5 status-icon availableIcon" />
          );
        },
        cellClass: "text-muted w-16",
        Cell: (props) => <>{props.value}</>,
        sortType: "basic",
      },
      {
        Header: "Name",
        accessor: "name",
        cellClass: "text-muted w-16",
        Cell: (props) => <>{props.value}</>,
        sortType: "basic",
      },
      {
        Header: "Age",
        accessor: (row) => {
          let createTime = new Date(row.creationdate);
          createTime = Math.abs(Date.now() - createTime) / (1000 * 3600 * 24);
          Math.floor(createTime);
          if (Math.floor(createTime) === 1) {
            return `${Math.floor(createTime)} day`;
          }
          return `${Math.floor(createTime)} days`;
        },
        cellClass: "text-muted w-16",
        Cell: (props) => <>{props.value}</>,
        sortType: "basic",
      },
      {
        Header: "Model Endpoint",
        accessor: "externalURL",
        cellClass: "text-muted w-16",
        Cell: (props) => <>{props.value}</>,
        sortType: "basic",
      },
      {
        Header: " ",
        accessor: (row) => {
          return (
            <a
              href={`/grafana/d/Katonic/prediction-analytics?orgId=1&refresh=5s&from=now-1m&to=now&var-deployment=${row.name}`}
              target="_blank"
              rel="noreferrer"
            >
              <i className="simple-icon-speedometer text-primary font-weight-bold h5" />
            </a>
          );
        },
        cellClass: "text-muted w-16",
        Cell: (props) => <>{props.value}</>,
        sortType: "basic",
      },
    ],
    []
  );

  return (
    <Card className="h-100 table-container">
      <CardBody>
        <CardTitle>
          <IntlMessages id={textHead} />
        </CardTitle>
        <Table columns={cols} data={serverList} />
        {loading === true ? <div className="loading" /> : null}
      </CardBody>
    </Card>
  );
};

export default ModelServers;
