import React from 'react';
import { Row } from 'reactstrap';
import { Colxx } from '../../../../components/common/CustomBootstrap';
import ModelServers from '../../../../components/custom/model/ModelServers';

const servers = () => {
  return (
    <>
      <Row>
        <Colxx xxs="12">
          <ModelServers textHead="model.modelServers" />
        </Colxx>
      </Row>
    </>
  );
};

export default servers;
